
/* General body and html settings */
html, body, #root {
  
  width: 100vw;
  min-height: 100vh; /* Ensures the full height is used without overflow */
  overflow: hidden;
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  @media (max-width: 768px) {
    height: auto; /* Allows content to adapt on mobile */
  }
}

/* Avoid margin collapse */
*, *::before, *::after {
  box-sizing: inherit;
  background-color: transparent;
}

button {
  background-color: transparent;
  border: none;
}

.App {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
}

.App-header {
  background-color: #850e35;
  color: white;
  display: flex;
  align-items: center;
  width: 100%;
  height: 5rem;
  @media (max-width: 768px) {
    height: 3rem; /* Adjust height for mobile to make more room */
    /* Adjust height for mobile to make more room */
 }
}

.navbar {
  background-color: #640726;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}

.dailyLogin {
  border: 1px solid fuchsia;
  height: 4rem;
  position: absolute;
  right: 15rem;
  transition: transform 0.5s ease; /* Smooth transition for growth */
  border-radius: 25rem;
  @media (max-width: 768px) {
    /* Adjust position for mobile to make more room */
    right: 12rem;
    height: 3rem;
  }
}

.dailyLogin:hover {
  transform: scale(1.05); /* Slightly grow the element */
  animation: glow 2s infinite alternate; /* Apply glow animation */
}

.discordLink {
  position: absolute;
  right: 22rem;
}

@keyframes glow {
  0% {
    background-color: #ff8ab8; /* Initial color */
  }
  50% {
    background-color: #ff004f; /* Darker or brighter red/pink for glow */
  }
  100% {
    background-color: #ff8ab8; /* Darker or brighter red/pink for glow */
  }
}

@keyframes textGlow {
  0% {
    text-shadow: 
      0 0 5px #ff8ab8,  /* Light pink */
      0 0 10px #ff7aa8, /* Slightly darker pink */
      0 0 20px #ff6a98, /* Mid pink */
      0 0 30px #ff5a88, /* Darker pink */
      0 0 40px #ff4a78; /* Deep pink */
  }
  50% {
    text-shadow: 
      0 0 5px #ffcbe0,  /* Lightest pink */
      0 0 15px #ffa8c5, /* Soft pink */
      0 0 25px #ff85aa, /* Brighter pink */
      0 0 35px #ff628f, /* Vibrant pink */
      0 0 45px #ff3f74; /* Deep pink */
  }
  100% {
    text-shadow: 
      0 0 5px #ff8ab8,  /* Light pink */
      0 0 10px #ff7aa8, /* Slightly darker pink */
      0 0 20px #ff6a98, /* Mid pink */
      0 0 30px #ff5a88, /* Darker pink */
      0 0 40px #ff4a78; /* Deep pink */
  }
}




.main-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  width: 100%;
  height: 100%;
  background-color: rgb(71, 18, 26);
  color: white;
  @media (max-width: 768px) {
    /* Adjust height for mobile to make more room */
    height: auto;
  }
}

.character-room-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgb(71, 18, 26);
  color: white;
  @media (max-width: 768px) {
    /* Adjust height for mobile to make more room */
    height: auto;
  }
}

.character-room {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 2vw;
  padding-top: 5vh;
  padding-bottom: 5vh;
  padding-left: 4vw;
  padding-right: 4vw;
  width: 80%;
  gap: 4vw;
  overflow-y: auto;
  scrollbar-width: none;
  min-height: 90vh;
  max-height: 90vh; /* Allows it to scroll within a constrained area */
  @media (max-width: 768px) {
    max-height: 90vh; /* Adjust height for mobile to make more room */
    scrollbar-width: none;
    justify-content: space-between;
    padding: 1rem 2rem 5rem 2rem;
  }
}

.character-filters {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1rem 1rem;
  border-radius: 25rem;
  width: 50%;
  border: 1px solid black;
  @media (max-width: 900px) {
    display: none;   
  }
 
}

.character-filters-dropdown {
  display: none;
  color: black;
  @media (max-width: 900px) {
    display: flex;
    
  }
}

.character-filters > button {
  color: #ff8ab8;
  font-size: 1.5rem;
  text-shadow: black 1px 1px 2px;
  font-weight: 600;
}

.character-filters > button:hover {
  color: #d02167;
  animation: textGlow 2s infinite alternate; /* Apply glow animation */
  font-weight: 600;
}


/* Vixens */

@keyframes rainbowGlow {
  0% {
    box-shadow: 0 0 1px 1px #ff4d4d, 0 0 2px 1px #ff6666, 0 0 3px 2px #ff8080, 0 0 4px 2px #ff99aa, 0 0 5px 3px #ffb3c1, 0 0 6px 3px #ffccd9, 0 0 7px 4px #ffe6f2;
    border-radius: 1rem;
  }
  50% {
    box-shadow: 0 0 1px 1px #ffe6f2, 0 0 2px 1px #ffccd9, 0 0 3px 2px #ffb3c1, 0 0 4px 2px #ff99aa, 0 0 5px 3px #ff8080, 0 0 6px 3px #ff6666, 0 0 7px 4px #ff4d4d;
    border-radius: 1rem;
  }
  100% {
    box-shadow: 0 0 1px 1px #ff4d4d, 0 0 2px 1px #ff6666, 0 0 3px 2px #ff8080, 0 0 4px 2px #ff99aa, 0 0 5px 3px #ffb3c1, 0 0 6px 3px #ffccd9, 0 0 7px 4px #ffe6f2;
    border-radius: 1rem;
  }
}



.vixen-card {
  width: 20%;
  height: 10%;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: transform 0.2s, box-shadow 0.2s;
  @media (max-width: 768px) {
    width: 40%;
    height: auto; /* Allows content to adapt on mobile */
  }
}

.vixen-card:hover {
  transform: scale(1.05);
}

.vixen-profile-card {
  width: 9rem;
  height: 12rem;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  box-shadow: inset 0 10px 25px rgba(0, 0, 0, 0.429);
}

.vixen-profile-card.shimmer {
  animation: rainbowGlow 3s linear infinite;
}

.vixen-info {
  width: 100%;
  height: 30%;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0);
}

.vixen-details {
  font-weight: 700;
  text-shadow: black 1px 1px 2px;
  cursor: default;
}

/* Chat Rooms */

.chatrooms {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
}

/* Chatroom */
.chatBox {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 95vh; /* Adjusted for navbar and chatBar */
  

  @media (max-width: 768px) {
    height: calc(100vh - 12vh); /* Adjusted for mobile */
  }
}

.titleBar {
  background-color: rgb(145, 35, 51);
  color: white;
  display: flex;
  align-items: center;
  width: 100%;
  height: 8rem;
  text-align: center;
  justify-content: space-between;
  padding: 0.5rem;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.669);
}

.titlePic {
  height: 7rem;
  width: 7rem;
  border-radius: 50%;
  margin-right: 1%;
  margin-left: 5%;
}

.chatSettings {
  width: 4rem;
  height: 4rem;
  font-size: 3rem;
}

.chatTitle {
  font-size: 3rem;
  font-weight: 500;
  color: white;
  text-shadow: 1px 1px 2px black;
}


.chatMessages {
  flex-grow: 1;
  overflow-y: auto;
  padding: 1rem;
  padding-bottom: 5rem;
  scrollbar-width: none;
  scroll-behavior: smooth;
  @media (max-width: 768px) {
    max-height: 80vh; /* Ensures content fits without extra space */
  }
}





/* Chat room messaging */

i {
  color:rgb(16, 16, 16);
  font-weight: 700;
  text-shadow: none;
}

.speech-text {
  color:rgb(255, 255, 255);
  font-weight: 700;
  text-shadow: 1px 1px 2px black;
}


.editLastMessage {
  
  position: relative;
  right: 4rem;
  top: -0.5rem;
  display: flex;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 1.75rem;
  @media (max-width: 768px) {
    height: 1.5rem; /* Ensures content fits without extra space */
    width: 1.5rem;
  }
}

.editIcon {
  width: 2.5rem;
  height: 2.5rem;
}

.editContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: flex-start;
}

.editInput {
  min-width: 100%;
  min-height: 10rem;
  resize: vertical; /* Allow vertical resizing */
  display: block; /* Make the input a block-level element */
  background-color: rgb(56, 56, 56); /* Adjust background color */

  border-radius: 5px 5px 0 0; /* Rounded corners */
  color: rgb(255, 255, 255); /* Text color */

  padding: 5px 10px; /* Padding for spacing */
  max-height: 90vh; /* Max height to avoid overflowing */
  max-height: 90dvh; /* For dynamic viewport height */
  box-sizing: border-box; /* Ensure padding is part of the total size */
  word-wrap: break-word; /* Allow long words to wrap */
  overflow: auto; /* Add scrollbars if content overflows */
}

.editOptions {
  z-index: 10;
  left: 59.5%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  border-radius: 0 0 5px 5px;
  gap: 0.5rem;
  background-color: rgb(0, 0, 0); /* Adjust background color */

}

.cancelEdit {
  height: 100%;
  font-size: 1.5rem;
}
.saveEdit {
  height: 100%;
  font-size: 1.5rem;
}
.copyMessage {
  height: 100%;
  font-size: 1.5rem;
}
.deleteMessage {
  color: #ff004f;
  height: 100%;
  font-size: 1.5rem;
}
.moveUp {
  height: 100%;
  font-size: 1.5rem;
}
.moveDown {
  height: 100%;
  font-size: 1.5rem;
}




.chatMessage {
  position: relative;
  display: flex;
  font-size: 1.25rem;
  width: 100%;
  min-height: 5vh; /* Same as the minimum height for consistency */
  margin-bottom: 1rem;
  align-items: flex-start; /* Align content to top-left */
  gap: 1rem; /* Adds space between icon and content */
}

.chatIcon {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
}

.chatMessageContent {
  
  left: 10rem;
  background-color: rgb(227, 132, 132);
  color: rgb(255, 255, 255);
  font-weight: 700;
  text-shadow: 1px 1px 2px black;
  width: 75%;
  border-radius: 1rem;
  padding: 1rem;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  word-wrap: break-word;
  overflow-wrap: break-word;
  box-sizing: border-box;
}

.chatBar {
  background-color: #640726;
  width: 100%;
  min-height: 6rem;
  height: fit-content; /* Increase height for mobile */
  display: flex;
  position: sticky;
  bottom: 5rem;
  align-items: center;
  justify-content: center;
  box-shadow: 0 -1px 14px rgba(0, 0, 0, 0.669);
  color: rgba(0, 0, 0, 0.607);
  @media (max-width: 768px) {
    padding-bottom: 2rem;
    /* Adjust height for mobile to make more room */
 }

}

.userInput {
  background-color: #ffb3c1;
  position: relative;
  width: 85vw;
  min-height: 5rem;
  border-radius: 1rem;
  padding: 1rem;
  resize: vertical;
}

.sendIcon {
     position:relative;
    width: 2rem; /* Ensures content fits without extra space */
    height: 2rem; /* Ensures content fits without extra space */
    margin-left: 1rem;
    @media (max-width: 768px) {
      width: 1.5rem; /* Ensures content fits without extra space */
      height: 1.5rem; /* Ensures content fits without extra space */
      margin-left: 0.5rem;
    }
}

.aiControls {
  width: 4rem;
  height: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.previousResponse {
  font-size: 2rem;

}

.nextResponse {
  font-size: 2rem;
}



/* Consent Modal */
.consent-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.consent-content {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.consent-content h2 {
  margin-bottom: 10px;
}

.consent-content button {
  margin: 5px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
