/* Modal.css */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(17, 3, 3, 0.886);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  padding: 1.5rem;
  border-radius: 8px;
  max-width: 500px;
  width: 100%;
  position: relative;
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.modal-content.activeModal {
  opacity: 1;
  transform: scale(1);
}

.modal-close {
  position: absolute;
  top: 0;
  right: 0;
  background: none;
  border: none;
  cursor: pointer;
  border-radius: 25rem;
  font-weight: 800;
  padding: 0 0.4rem 0.2rem;
  border: 2px solid fuchsia;
}

.token-title {
  font-size: 1rem;
}

.tokenPurchaseOptions {
	background-color: #450410;
	display: flex;
	justify-content: space-between;
	margin-top: 1rem;
	flex-direction: column;
	text-align: center;
	gap: 1.5rem;
	padding-bottom: 4rem;
	padding-left: 1rem;
	padding-right: 1rem;
	border-radius: 2rem;
	height: 27rem;
	animation: goldPinkGlow 1s ease-in-out infinite;
  }
  
  @keyframes goldPinkGlow {
	0% {
	  box-shadow: 0 0 8px 4px #ffd700, 0 0 12px 8px #ff69b4, 0 0 16px 12px #ffd700;
	}
	50% {
	  box-shadow: 0 0 12px 6px #ff69b4, 0 0 16px 10px #ffd700, 0 0 20px 14px #ff69b4;
	}
	100% {
	  box-shadow: 0 0 8px 4px #ffd700, 0 0 12px 8px #ff69b4, 0 0 16px 12px #ffd700;
	}
  }

.purchaseItem {
  border: 2px solid #c64545;
  display: flex;
  height: 4rem;
  flex-direction: row;
  align-items: center;
  padding: 0 1rem;
  border-radius: 1rem;
}

.baseAmountPurchase {
  font-weight: bold;
  font-size: 2rem;
  color: rgb(186, 143, 0);
  margin-left: 0.75rem;
  @media (max-width: 768px) {
	font-size: 1.25rem;
  }
}

.bonusPurchaseAmount {
  font-size: 1.5rem;
  color: #c72280;
  @media (max-width: 768px) {
	font-size: 1rem;
  }
}

.tokenPurchaseIcon {
  width: 2rem;
  height: 2rem;
}

.purchasePrice {
  position: absolute;
  right: 3rem;
  background-color: #c72280;
  padding: 0 1rem;
  border-radius: 25rem;
}

.mostPopular {
	width: 4rem;
	height: 4rem;
	position: absolute;
	top: 3.5rem;
	right: 1rem;
	@media (max-width: 768px) {
	}
}


.dailyLoginContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 2rem;
  background-color: rgb(60, 10, 10);
  border-radius: 4rem;
  box-shadow: 0px 4px 8px rgb(0, 0, 0);
  height: 30rem;
  @media (max-width: 768px) {
    width: 100%; /* Adjust width for mobile to make more room */
    padding: 1rem;
    height: 30rem;
	border-radius: 2rem;
  };
  animation: goldPinkGlow 2s ease-in-out infinite;
}

.dailyLoginTiers {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 2rem;
  @media (max-width: 768px) {
    gap: 1rem;
	
  }
}

.dailyLoginRewards {
  width: 100%;
  display: flex;
  flex-direction: column;

}

.daily0,
.daily1,
.daily2,
.daily3,
.daily4,
.daily5,
.daily6 {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding: 0.6rem 1rem;
  height: 13.5%;
  align-items: center;

}

.redeemfree0Daily,
.redeemfree1Daily,
.redeemfree2Daily,
.redeemfree3Daily,
.redeemfree4Daily,
.redeemfree5Daily,
.redeemfree6Daily {
  cursor: pointer;
  border: 2px solid #c64545;
  padding: 0.25rem 0.25rem;
  border-radius: 0.4rem;
}

.redeempremium0Daily,
.redeempremium1Daily,
.redeempremium2Daily,
.redeempremium3Daily,
.redeempremium4Daily,
.redeempremium5Daily,
.redeempremium6Daily {
  cursor: pointer;
  border: 2px solid #c64545;
  padding: 0.25rem 0.25rem;
  border-radius: 0.4rem;
}

.dailyLoginRewards > div > button {
  font-size: 1rem;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 5px;
}

.dailyLoginRewards > div > button:not(:disabled) {
	background-color: #f34040;
  color: white;
  box-shadow: 0 0 10px rgba(216, 0, 0, 0.7), 
              0 0 20px rgba(216, 0, 0, 0.5), 
              0 0 30px rgba(216, 0, 0, 0.3);
  animation: glow 1s infinite alternate;
}

.dailyLoginRewards > div > button:disabled 
/* Gray-out style for disabled buttons */{
	background-color: #8b1d1d;
  color: white;
  box-shadow: 0 0 10px rgba(216, 0, 0, 0.7), 
              0 0 20px rgba(216, 0, 0, 0.5), 
              0 0 30px rgba(216, 0, 0, 0.3);
  }

/* Default styling for the day text */
.dayText {
	font-size: 16px;
	margin-top: 5px;
	transition: all 1s ease;
  }
  
  /* Glowing effect for the current day */
  .glowingDay {
	color: #d80070;
	font-weight: bold;
	text-shadow: 0 0 5px rgba(216, 0, 0, 0.7),
				 0 0 10px rgba(216, 0, 0, 0.5),
				 0 0 15px rgba(216, 0, 0, 0.3);
	animation: glowText 1s infinite alternate;
  }
  
  @keyframes glowText {
	from {
	  text-shadow: 0 0 1rem rgba(216, 0, 0, 0.5),
				   0 0 1rem rgba(216, 0, 0, 0.3);
	}
	to {
	  text-shadow: 0 0 1rem rgba(216, 0, 0, 0.8),
				   0 0 1rem rgba(216, 0, 0, 0.6);
	}
  }

  @keyframes glow {
	from {
	  box-shadow: 0 0 5px rgba(216, 0, 137, 0.5),
				  0 0 10px rgba(216, 0, 166, 0.3);
	}
	to {
	  box-shadow: 0 0 15px rgba(216, 0, 209, 0.8),
				  0 0 25px rgba(216, 0, 158, 0.6);
	}
  }

  button {
  pointer-events: auto;
  touch-action: manipulation; /* Improves responsiveness on mobile */
}
