/* Container */
.side-menu-container {
	position: relative;
	left: 2rem;
	height: 3rem;
	width: 3rem;
	padding: 0;
  }

/* Button styling */
  .menu-button {
	z-index: 1;
	background-color: transparent;
	color: #fff;
	cursor: pointer;
	height: 100%;
	width: 100%;
	border: none;
  }

  /* Menu Icon */
  .menu-icon {
	width: 2rem;
	height: 2rem;
  }


  .token-stash {
	background-color: #4c0a0a;
	border-radius: 25rem;
	height: 3rem;
	display: flex;
	justify-content: space-between;
	gap: 1rem;
	align-items: center;
	position: relative;
	right: 2%;
	font-size: 2rem;
	font-weight: 500;
	text-shadow: black 1px 1px 1px;
	border: 1px solid black;
	padding: 0 0.5rem;
	@media (max-width: 768px) {
		gap: 0.25rem;
		width: 10rem;
		height: 2rem;
		font-size: 1.5rem;
	  }
	
  }

  .token-stash-icon{
	height: 3rem;
	width: 3rem;
	border: 2px solid black;
	border-radius: 50%;
	@media (max-width: 768px) {
		width: 2rem;
		height: 2rem;
	  }
  }

  .buyTokensButton {
	border: 3px solid black;
	border-radius: 50rem;
	height: 2rem;
	width: 2rem;
	display: flex;
	justify-content: center;
	text-align: center;
	align-items: center;
	background-color: white;
	color: black;
    font-size: 2rem;
	font-weight: 600;
	@media (max-width: 768px) {
		height: 1.5rem;
		width: 1.5rem;
	  }
  }
  


  
  /* Side Menu */
  .side-menu {
	z-index: 5;
	position: fixed;
	top: 0;
	left: -50%;
	min-width: 20%;
	max-width: 20%;
	height: 100vh;
	background-color: #333;
	color: white;
	font-size: large;
	transition: left 0.3s ease;
	display: flex;
	@media (max-width: 768px) {
		min-width: 35vw;
	  }
  }
  
  .side-menu.open {
	left: 0;
	box-shadow: 0 4px 2rem rgba(0, 0, 0, 0.719);
  }
  
  /* Menu Items */
.side-menu > div {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
    background-color: rgb(71, 18, 26);
    color: #fff;
	
}

/* Side Menu Top */
.side-menu-top {
	width: 100%;
	display: flex;
	flex-direction: row;
	border-bottom: 1px solid #fff;
	padding: 1rem;
	background-color: rgb(46, 12, 17); /* Ensures background is the same as the menu */
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.719);
	z-index: 10;
	@media (max-width: 768px) {
		padding: 1rem 0;
	  }
}

.side-menu-title {
	height: 15%;
}



.signout {
	position: absolute;
	bottom: 2.5%;
	padding: 10px 20px;
	font-size: 16px;
	font-weight: bold;
	color: #ffffff;
	background-color: #ff4d4d;
	border: 2px solid #ab0000;
	border-radius: 8px;
	cursor: pointer;
	transition: transform 0.2s ease, background-color 0.2s ease, box-shadow 0.2s ease;
	@media (max-width: 768px) {
		width: 50%;
		height: 5%;
		padding: 0 1rem;
		bottom: 7.5%;
		font-size: 0.7rem;
	  }
  }
  
  .signout:hover {
	background-color: #ff1a1a;
	border: 2px solid #8e0000;
	box-shadow: 0px 8px 15px rgba(255, 77, 77, 0.3);
	transform: scale(0.95);
  }
  
  .signout:active {
	transform: scale(1);
	box-shadow: 0px 6px 12px rgba(255, 77, 77, 0.2);
  }

  .signin {
	height: 10%;
	display: flex;
	align-items: center;
	padding: 10px 20px;
	font-size: 16px;
	font-weight: bold;
	color: #ffffff;
	background: linear-gradient(90deg, #4285f4, #34a853, #fbbc05, #ea4335);
	background-size: 300%;
	border: none;
	border-radius: 8px;
	cursor: pointer;
	transition: background-position 0.4s ease, transform 0.3s ease, box-shadow 0.3s ease;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .signin:hover {
	background-position: 100%;
	transform: scale(1.05);
	box-shadow: 0 8px 16px rgba(66, 133, 244, 0.4);
  }
  
  .signin:active {
	transform: scale(1);
	box-shadow: 0 6px 12px rgba(66, 133, 244, 0.3);
  }

  .recentChats {
	width: 90%;
	height: 50%;
	border: 1px solid hotpink;
	display: flex;
	flex-direction: column;
	position: absolute;
	top: 25%;

  }

  .vixen-chat-selector {
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow: scroll;
	scrollbar-width: none;
  }

  .vixen-chat-item:nth-child(even) {
	background-color: rgb(157, 34, 53); /* Replace with your desired color */
  }

.vixen-chat-item {
	text-shadow: black 1px 1px 1px;
	width: 100%;
	height: 30%;
	padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid darkgray;
}

.vixen-chat-pic {
	width: 4rem;
	height: 4rem;
	border-radius: 25rem;
}
.homeButton {
	width: 100%;
	margin-top: 1rem;
	border: 1px solid gold;

}

.recentChatHistoryTitle {
	width: 100%;
	text-align: center;
	font-size: 2rem;
	font-weight: bold;
	text-shadow: black 1px 1px 1px;
	@media (max-width: 768px) {
		font-size: 1.5rem;
	  }
}

.userLinks {
	position: relative;
	width: 100%;
	margin-top: 1rem;
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
	/* Optional: space between grid items */
  align-content: center;
  height: 10%;
  border: 1px solid white;
}

.discordButtonSide {
	border: 1px solid #ff99aa;
	border-radius: 25rem;
	height: 3rem;
	width: 3rem;

}

.userWelcome {
	position: absolute;
	font-size: 1rem !important;
	width: 100%;
	left: 0.5rem;
	font-weight: bold;
	text-shadow: black 1px 1px 1px;
}

.userUid {
	position: absolute;
	color: rgb(83, 83, 83);
	font-size: 0.75rem;
	width: 100%;
	bottom: 0;
	left: 0.5rem;
	@media (max-width: 768px) {
		font-size: 0.5rem;
	  }
}

@media (max-height: 1280px) {
	.userWelcome {
		font-size: 1rem;
	}

}

@media (max-height: 660px) {
	.recentChats {
		top: 40%;
	}
}